.popup {
  position: fixed;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  bottom: 0;
  top: 0;
  background: rgb(0%, 0%, 0%, 0.4);;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 110;
  display: none;
  &__content {
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family    : "Antenna-Medium";
    background: linear-gradient(325.95deg, rgba(111, 220, 255, 0.35) -34.95%, rgba(0, 0, 30, 0.35) 100%);
    backdrop-filter: blur(13px);
    border-radius: 16px;
    padding: 70px 0 40px 0;
    z-index: 50;
    @media (max-width: 768px) {
      max-width: 85%;
      padding: 50px 0 35px 0;
    }
    h3 {
      font-weight: 500;
      font-size: 30px;
      line-height: 104%;
      color: #FFFFFF;
      @media (max-width: 768px) {
        font-size: 18px;
      }
    }
  }
  &__btn {
    margin-top: 25px;
    background: linear-gradient(129.27deg, rgba(111, 220, 255, 0.45) -22.87%, rgba(255, 36, 141, 0.45) 121.44%);
    border-radius: 10px;
    width: 100px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 100%;
    }
    &:hover {
      cursor: pointer;
    }
    p {
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
      text-align: center;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: white;
      @media (max-width: 768px) {
        font-size: 12px;
        line-height: 17px;
      }
    }
  }
}
